.top {
    color: ghostwhite;
}

.aboutMe img {
    width: 250px;
    height: 250px;
    opacity: 1;
    border-radius: 50%;
    float: left;
    padding: 10px ;
    shape-outside: inset(1% round 50%);
    shape-margin: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
}

.aboutMe p {
    line-height: 1.4;
    text-align: justify;
    /* margin: 0; */
    font-size: 23px;
}

.aboutMe h3 {
    font-size: 2rem;
    text-align: center;
    letter-spacing: 2px;
}

.aboutMe h5 {
    letter-spacing: 2px;
}

#readOpac{
    padding: 15px;
    background: rgba(0,0,0, 0.4) !important;
    border-radius: 6.9px;
}

.btn-link {
    display: block;
    margin: auto auto;
}

.btn-link button {
    width: 100px !important;
    height: 100px;
    padding: 9px;
    margin: 80px 0;
}

.btn-link button:hover {
    background-color: blue;
    border: 1px solid gold !important;
    animation: flip-animation .69s alternate ease-in-out;
    display: inline-block;
}

@keyframes flip-animation {
    0% {
        transform: rotate(0deg);
    }  
    100% {
        transform: rotate(359deg);
        
    }
}

@media only screen and (max-width: 600px) {
    .top {
        display: block;
    }
    .aboutMe{
        width: 200px;
    }

    div .btn-link {
        left: -50px;
    }
   
    .aboutMe h5,
    .aboutMe p{
        font-size: 15px;
    }
    #readOpac{
        /* box-sizing: content-box !important; */
       
        padding: 10px;
    }
    .aboutMe img {
        width: 150px;
        height: 150px;
       
    }
    
}