@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Archivo:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.navbar{
   
    
    opacity: 100%;
    z-index: 9;
}

img.logo {
    height: 150px;
    width: 150px;
}
.top {
    color: ghostwhite;
}

.aboutMe img {
    width: 250px;
    height: 250px;
    opacity: 1;
    border-radius: 50%;
    float: left;
    padding: 10px ;
    -webkit-shape-outside: inset(1% round 50%);
            shape-outside: inset(1% round 50%);
    -webkit-shape-margin: 10px;
            shape-margin: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
}

.aboutMe p {
    line-height: 1.4;
    text-align: justify;
    /* margin: 0; */
    font-size: 23px;
}

.aboutMe h3 {
    font-size: 2rem;
    text-align: center;
    letter-spacing: 2px;
}

.aboutMe h5 {
    letter-spacing: 2px;
}

#readOpac{
    padding: 15px;
    background: rgba(0,0,0, 0.4) !important;
    border-radius: 6.9px;
}

.btn-link {
    display: block;
    margin: auto auto;
}

.btn-link button {
    width: 100px !important;
    height: 100px;
    padding: 9px;
    margin: 80px 0;
}

.btn-link button:hover {
    background-color: blue;
    border: 1px solid gold !important;
    -webkit-animation: flip-animation .69s alternate ease-in-out;
            animation: flip-animation .69s alternate ease-in-out;
    display: inline-block;
}

@-webkit-keyframes flip-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }  
    100% {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
        
    }
}

@keyframes flip-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }  
    100% {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
        
    }
}

@media only screen and (max-width: 600px) {
    .top {
        display: block;
    }
    .aboutMe{
        width: 200px;
    }

    div .btn-link {
        left: -50px;
    }
   
    .aboutMe h5,
    .aboutMe p{
        font-size: 15px;
    }
    #readOpac{
        /* box-sizing: content-box !important; */
       
        padding: 10px;
    }
    .aboutMe img {
        width: 150px;
        height: 150px;
       
    }
    
}
:root {
  --primary-white: #fff;
}

body {
  font-family: 'Archivo', sans-serif !important;
  
  height: 100%;
  background-image: url(/static/media/aboutBG.0cabd88b.jpg);
  background-attachment: fixed;
  background-position: bottom ;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}
/* **************** NAVBAR ************************ */

.logo {
  width: 4.25rem;
}



div .top {
    margin-top: -5%;
}

.card {
    background: rgba(0, 0, 0, 0.03) !important;
    text-align: center;
    font-weight: 500;
    display: block !important;
    height: 429px;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -webkit-hyphens: auto;
        -ms-hyphens: auto;
            hyphens: auto;
    margin: 0 30px 0 30px;
    max-width: 325px;
}

.card:hover {
    background: rgba(0, 0, 0, 0.69) !important;
    -webkit-transform: scale(1.069);
            transform: scale(1.069);
    transition: .33s ease-in-out;
    border: .69px solid gold !important;
}

.card img {
    height: 175px;
    background-size: auto;
}

.card-bottom {
    position: absolute;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    bottom: -12px;
    font-size: 20px;
    left: 10px;
}

.card-bottom a {
    -webkit-animation: spin-animation 2.314s infinite;
            animation: spin-animation 2.314s infinite;
    display: inline-block;
    /* transition: all .2s ease-in-out; */
}

@-webkit-keyframes spin-animation {
    0% {
        -webkit-transform: scale(.7);
                transform: scale(.7)
    }
    100% {
        -webkit-transform: scale(1.25);
                transform: scale(1.25)
    }
}

@keyframes spin-animation {
    0% {
        -webkit-transform: scale(.7);
                transform: scale(.7)
    }
    100% {
        -webkit-transform: scale(1.25);
                transform: scale(1.25)
    }
}

.card-bottom a:hover {
    color: gold !important;
}

.card-body {
    margin: 0 -25px 0 -25px;
}

.card-body ul {
    text-align: start;
    font-size: 12px;
    font-weight: bolder;
}

.mNp {
    margin: 90px;
}

.card-title {
    font-size: 16px;
}

@media only screen and (max-width: 600px) {
    .card {
        height: 200px;
    }
    .top h1 {
        font-size: 35px;
        text-align: center;
    }
    .card-body ul {
        display: none;
    }
    .card img {
        height: 100px;
    }
}
.behave {
    margin-top: 69px;
    display: block;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}

.left{
    margin-left: 40px;
}

.behave p {
    color: ghostwhite;
    font-weight: 900;
    font-size: 29px;
}

.behave img {
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

.behave img {
    -webkit-animation: pulse-animation 4s infinite;
            animation: pulse-animation 4s infinite;
    display: inline-block;
}

@-webkit-keyframes pulse-animation {
    0% {
        border: 2.3px inset green;
    }
    30% {
        border: 2.3px inset red;
    }
    50% {
        border: 2.3px inset gold;
    }
    80% {
        border: 2.3px inset blue;
    }
    100% {
        border: 2.3px inset orange;
    }
}

@keyframes pulse-animation {
    0% {
        border: 2.3px inset green;
    }
    30% {
        border: 2.3px inset red;
    }
    50% {
        border: 2.3px inset gold;
    }
    80% {
        border: 2.3px inset blue;
    }
    100% {
        border: 2.3px inset orange;
    }
}

.behave p {
    -webkit-animation: text-animation 9s infinite;
            animation: text-animation 9s infinite;
    display: inline-block;
}

@-webkit-keyframes text-animation {
    0% {
        color: green !important;
    }
    30% {
        color: red;
    }
    50% {
        color: gold;
    }
    80% {
        color: blue;
    }
    100% {
        color: ghostwhite;
    }
}

@keyframes text-animation {
    0% {
        color: green !important;
    }
    30% {
        color: red;
    }
    50% {
        color: gold;
    }
    80% {
        color: blue;
    }
    100% {
        color: ghostwhite;
    }
}

.bRow1 {
    display: block;
    margin: auto;
    text-decoration: none;
}

.bRow2 {
    display: block;
    margin: auto;
    text-decoration: none;
}

@media only screen and (max-width: 600px) {
    .behave {
        margin: 0;
        padding: 0;
        display: block;
        margin: auto;
    }

    .left{
        margin-left: 0;
    }
}
